.not-found-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 50px 0;
    height: 75vh;
    width: 80%;
    margin: auto;
}
.not-found-container img{
    max-width: 250px;
    height: auto;
    margin-bottom: 20px;
}
.not-found-container h4{
    font-size: 36px;
    margin-bottom: 10px;
}
.not-found-container p{
    font-size: 18px;
}