footer {
  display: flex;
  flex-direction: row;
  margin: 0;
  padding: 0;
  background-color: var(--primary-color);
  color: var(--white);
  padding: 30px 50px;
  justify-content: space-around;
}
footer p {
  font-size: 14px;
}
@media only screen and (max-width: 700px) {
  footer {
    flex-direction: column;
    text-align: center;
    padding: 20px 30px;
  }
  footer p {
    margin: 5px 0;
  }
}
@media only screen and (max-width: 1024px) and (min-width: 768px) {
  footer {
    flex-direction: column;
    text-align: center;
  }
  footer p {
    margin: 5px 0;
  }
}
