.about-main-wrapper-section {
  padding: 70px 0;
  background-color: var(--light-grey);
}
.about-main-wrapper-section .about-wrapper {
  width: 90%;
  margin: auto;
}
.about-main-wrapper-section-2 {
  padding: 70px 0;
  background-color: var(--white);
}
.about-main-wrapper-section-2 .about-wrapper-2 {
  display: flex;
  flex-direction: row;
  width: 90%;
  margin: auto;
  color: var(--grey);
  flex-wrap: wrap;
}
.about-main-wrapper-section-2 .about-wrapper-2 h3 {
  font-size: 25px;
  font-weight: normal;
}
.about-main-wrapper-section-2 .about-wrapper-2 .about-column {
  display: flex;
  flex-direction: column;
  margin: 20px;
  height: auto;
  transition: 0.5s all ease-in-out;
  flex-wrap: wrap;
  flex: 1 1 300px;
}
.about-main-wrapper-section-2 .about-wrapper-2 .about-column h3 {
  margin-bottom: 20px;
}
.about-main-wrapper-section-2
  .about-wrapper-2
  .about-column
  .our-story-content
  p {
  margin-top: 0;
  padding-top: 0;
}
.about-main-wrapper-section-2
  .about-wrapper-2
  .about-column
  .our-story-content
  img {
  float: left;
  margin: 0 20px 10px 0;
}
.about-main-wrapper-section-2
  .about-wrapper-2
  .about-column
  .feedbacks
  .feedback {
  border-left: 1px solid var(--primary-color);
  border-right: 1px solid var(--primary-color);
  border-top: 1px solid var(--primary-color);
  display: flex;
  flex-direction: row;
  height: 40px;
  align-items: center;
  margin: 0;
  padding: 0 15px;
  font-size: 14px;
  color: var(--grey);
}
.about-main-wrapper-section-2
  .about-wrapper-2
  .about-column
  .feedbacks
  .feedback:nth-child(5) {
  border-bottom: 1px solid var(--primary-color);
}
.about-main-wrapper-section-2
  .about-wrapper-2
  .about-column
  .feedbacks
  .feedback
  .percentage {
  width: 9%;
  text-align: right;
}
.about-main-wrapper-section-2
  .about-wrapper-2
  .about-column
  .feedbacks
  .feedback
  .star,
.percentage {
  width: 17%;
}
.about-main-wrapper-section-2
  .about-wrapper-2
  .about-column
  .feedbacks
  .feedback
  .progress-bar {
  width: 74%;
}
.about-main-wrapper-section-2
  .about-wrapper-2
  .about-column
  .feedbacks
  .feedback
  .progress-bar
  div {
  background: var(--primary-color);
  height: 15px;
  margin: 0;
  padding: 0;
  line-height: 40px;
  border-radius: 20px;
}
