.contact-main-wrapper-section .contact-main-wrapper-details-section {
  background-color: var(--light-grey);
  padding: 70px 0;
}
.contact-main-wrapper-section .contact-main-wrapper-card {
  background-color: var(--white);
  padding: 70px 0;
}
.contact-main-wrapper-section
  .contact-main-wrapper-card
  .vertical-card-with-icon {
  width: 90%;
  margin: auto;
}
.contact-main-wrapper-section .contact-main-wrapper-details {
  display: flex;
  flex-direction: row;
  width: 90%;
  justify-content: space-between;
  margin: auto;
  flex-wrap: wrap;
}
.contact-main-wrapper-section .contact-main-wrapper-details .details {
  display: flex;
  flex-direction: column;
  transition: 0.5s all ease-in-out;
  flex: 1 1 300px;
  margin: 10px;
}
.contact-main-wrapper-section .contact-main-wrapper-details .details iframe {
  min-height: 415px;
  width: auto;
  margin: 10px 0;
  border: 1px solid var(--primary-color);
  outline: none;
}
.contact-main-wrapper-section .contact-main-wrapper-details input,
textarea,
button {
  padding: 15px 20px;
  margin: 10px 0;
  border: 1px solid var(--primary-color);
  outline: none;
}
.contact-main-wrapper-section .contact-main-wrapper-details button {
  background-color: var(--primary-color);
  color: var(--white);
  cursor: pointer;
}
