.main-navbar{
    height: 10vh;
    line-height: 10vh;
    background-color: var(--white);
}
.main-navbar.nav-position-fixed {
    position: fixed;
    left: 0;
    width: 100%;
    top: 0;
    z-index: 4444;
    box-shadow: 0 3px 6px rgb(57 95 157 / 15%), 0 3px 6px rgb(18 45 89 / 12%);
  }
.main-navbar .navbar{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.main-navbar .navbar .logo a{
    color: var(--dark);
    font-size: 25px;
    font-weight: 550;
    text-decoration: none;
}
.main-navbar .navbar .logo span{
    color: var(--primary-color);
    font-size: 25px;
}
.main-navbar .navbar .logo .navbar-logo-icon svg{
    vertical-align: unset;
}
.main-navbar .menu-bar{
    display: none;
    cursor: pointer;
    font-size: 28px;
}
.main-navbar .navbar{
    text-decoration: none;
    margin: 0;
    padding: 0 20px;
}
.main-navbar .navbar .nav-items a:not(.active):hover{
    color: var(--primary-color);
}
.main-navbar .navbar .nav-items .active{
    background-color: transparent;
    color: var(--white);
    background: var(--primary-color);
    border-radius: 10px;
    -webkit-box-shadow: 1px 1px 19px 0px rgb(96 201 235 / 43%);
    -moz-box-shadow: 1px 1px 19px 0px rgba(96, 201, 235, 0.43);
    box-shadow: 1px 1px 19px 0px rgb(96 201 235 / 43%);
}
.main-navbar .nav-items{
    font-size: 15px;
}
.main-navbar .nav-items a{
    text-decoration: none;
    margin: 0 15px;
    padding: 10px 15px;
    color: var(--primary-color);
    font-weight: 550;
}
.main-navbar .mobile-menu{
    background-color: var(--primary-color);
    color: var(--white);
    width: 80%;
    position: fixed;
    top: 0;
    right: -200px;
    transition: transform 836ms linear 900ms;
    backdrop-filter: blur(0px);
    -webkit-backdrop-filter: blur(0px);
    -webkit-animation-name: openMenu;
    animation-name: openMenu;
    animation-duration: 300ms;
    animation-fill-mode: both;
    z-index: 4444;
}
@-webkit-keyframes openMenu {
    0% {
      -webkit-transform: translate(-200px, 1px);
      transform: translate(-100px, 0px);
    }
    100% {
      -webkit-transform: translate(-200px, 1px);
      transform: translate(-200px, 0px);
    }
  }
  @keyframes openMenu {
    0% {
        -webkit-transform: translate(-200px, 1px);
        transform: translate(-100px, 0px);
      }
      100% {
        -webkit-transform: translate(-200px, 1px);
        transform: translate(-200px, 0px);
      }
  }
.main-navbar .mobile-menu .nav-items{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 40px 0;
}
.main-navbar .mobile-menu p{
    cursor: pointer;
    font-size: 28px;
    text-align: right;
    margin: 20px 20px 10px 0px;
}
.main-navbar .mobile-menu .nav-items a{
    text-decoration: none;
    margin: 10px 0;
    padding: 0;
    color: var(--white);
    font-weight: 550;
    font-size: 15px;
}
.main-navbar .mobile-menu .nav-items a.active{
    text-decoration: none;
    margin: 10px 0;
    padding: 0 30px;
    color: var(--primary-color);
    font-weight: 550;
    background-color: var(--white);
    border-radius: 20px;
}
@media only screen and (max-width: 700px) {
    .main-navbar .navbar .nav-items{
        display: none;
    }
    .main-navbar .menu-bar{
        display: block;
    }
}
@media only screen and (max-width: 1336px) and (min-width: 1024px) {
    .main-navbar{
        height: 9vh;
        line-height: 9vh;
    }
  }