.features-main-wrapper-section {
  padding: 70px 0;
  background-color: var(--light-grey);
}
.features-main-wrapper-section .features-main-wrapper {
  width: 90%;
  margin: auto;
}
.features-main-wrapper-section .features-wrapper {
  display: flex;
  flex-direction: row;
  color: var(--dark);
  justify-content: center;
  align-items: center;
}
.features-main-wrapper-section .features-wrapper ul {
  list-style: none;
  text-indent: -20px;
  padding: 20px 0 20px 30px;
}
.features-main-wrapper-section .features-wrapper ul li {
  font-size: 14px;
  list-style: none;
}
.features-main-wrapper-section .features-wrapper li::before {
  content: "✔";
  color: var(--primary-color);
  padding-right: 10px;
  width: 30px;
  text-indent: 0;

}
.features-main-wrapper-section .features-wrapper .features-content p {
  font-size: 16px;
  padding-right: 40px;
}
.features-main-wrapper-section .features-wrapper .features-content button {
  outline: none;
  border: 2px solid var(--primary-color);
  border-radius: 25px;
  padding: 10px 20px;
  color: var(--grey);
  cursor: pointer;
}

@media only screen and (max-width: 768px) {
    .features-main-wrapper-section .features-wrapper img {
    display: none;
  }
}
