.newsletter-wrapper {
  background-color: var(--light-grey);
  padding: 70px 0;
}
.newsletter {
  width: calc(90% - 40px);
  margin: auto;
  background-color: var(--primary-color);
  border-radius: 10px;
  text-align: center;
  padding: 36px 20px;
  color: var(--white);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
}
.testileftdot {
  position: absolute;
  left: 20px;
  bottom: 30px;
}
.testirightdot {
  position: absolute;
  right: 20px;
  top: 30px;
}
.newsletter h1 {
  font-size: 38px;
  letter-spacing: 3%;
  font-weight: bold;
  margin-bottom: 16px;
}
.newsletter p {
  font-size: 17px;
  margin-bottom: 22px;
}
.newsletter .newsDiv {
  border-radius: 50px;
  background-color: var(--white);
  margin: auto;
  display: flex;
  padding-left: 16px;
  justify-content: space-between;
  align-items: center;
  width: 40%;
}
.newsletter .newsDiv input {
  width: 80%;
  height: auto;
  border: none;
  outline: none;
  display: inline;
  border-radius: 50px;
  padding: 4px;
  color: #000;
}
.newsletter .newsDiv input::placeholder {
  font-size: 14px;
}
.newsletter .newsDiv .send-button {
  height: 40px;
  width: 40px;
  margin: 8px auto;
  background-color: var(--primary-color);
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 8px;
  border-radius: 50%;
  cursor: pointer;
}

@media only screen and (max-width: 768px) {
  .testileftdot,
  .testirightdot {
    display: none;
  }
  .newsletter h1 {
    font-size: 25px;
  }
  .newsletter p {
    font-size: 14px;
  }
  .newsletter .newsDiv {
    width: 90%;
  }
}
