.breadcrumb {
  color: var(--white);
  padding: 80px 0;
  text-align: center;
  background-image: url(../images/header.png);
  background-repeat: no-repeat;
  background-position: bottom left;
}
.breadcrumb h3,
p {
  padding: 10px 0;
}
.breadcrumb h3 {
  font-size: 30px;
}
.breadcrumb p {
  font-size: 16px;
}
@media screen and (max-width: 768px) {
  .breadcrumb h3 {
    font-size: 25px;
  }
  .breadcrumb p {
    font-size: 16px;
  }
}
@media screen and (min-width: 300px) and (max-width: 600px) {
  .breadcrumb h3 {
    font-size: 20px;
  }
  .breadcrumb p {
    font-size: 14px;
  }
}
