.find-tour {
  width: 90%;
  margin: 70px auto;
}
.find-tour-main-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  flex-wrap: nowrap;
}
.find-tour-main-wrapper .form-group {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  border: 1px solid var(--primary-color);
  padding: 12px 20px;
  margin: 0;
  vertical-align: middle;
  flex-wrap: nowrap;
  width: 100%;
  margin: 10px;
}
.find-tour-main-wrapper .form-group:is(.invalid-field) {
  border: 1px solid var(--danger);
}
.find-tour-main-wrapper .form-group:nth-child(3) {
  justify-content: flex-start;
}
.find-tour-main-wrapper .form-group:nth-child(4) {
  border: none;
  padding: 0;
}
.find-tour-main-wrapper .form-group input {
  margin: 0;
  outline: none;
  border: none;
  width: 100%;
}
.find-tour-main-wrapper .form-group button {
  margin: 0;
  outline: none;
  border: none;
  background-color: var(--primary-color);
  color: var(--white);
  cursor: pointer;
  width: 100%;
}
@media only screen and (max-width: 768px) {
  .find-tour-main-wrapper {
    flex-direction: column;
    flex-wrap: nowrap;
  }
  .find-tour-main-wrapper .form-group {
    width: calc(100vw - 95px);
  }
  .find-tour-main-wrapper .form-group:nth-child(4) {
    width: calc(100vw - 55px);
  }
  .find-tour-main-wrapper .form-group button {
    padding: 12px 20px;
  }
}
@media only screen and (max-width: 1024px) and (min-width: 768px) {
  .find-tour-main-wrapper {
    flex-direction: column;
    flex-wrap: nowrap;
  }
  .find-tour-main-wrapper .form-group {
    width: calc(100vw - 140px);
  }
  .find-tour-main-wrapper .form-group:nth-child(4) {
    width: calc(100vw - 95px);
  }
  .find-tour-main-wrapper .form-group button {
    padding: 12px 20px;
  }
}
@media only screen and (max-width: 1336px) and (min-width: 1024px) {
  .find-tour-main-wrapper .form-group {
    width: calc(100vw - 160px);
  }
  .find-tour-main-wrapper .form-group:nth-child(4) {
    width: calc(100vw - 120px);
  }
  .find-tour-main-wrapper .form-group button {
    padding: 12px 20px;
  }
}
.find-tour .buss-lists {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.find-tour .buss-lists .card {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  margin: 10px 10px;
  flex: 1 1 500px;
  box-shadow: 0 0 4px 0 hsl(0deg 0% 64% / 50%);
  padding: 1em 1em 0.5em;
  background: var(--white);
  cursor: pointer;
  border-radius: 5px;
  width: calc(100% - 50px);
}
.find-tour .buss-lists .card .card-body .card-header-info {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 0;
  padding: 0;
}
.find-tour .buss-lists .card .card-body .card-header-info .card-title {
  margin-top: 0;
  color: var(--dark);
  font-weight: bold;
}
.find-tour .buss-lists .card .card-body .card-header-info p {
  color: var(--grey);
  font-weight: bold;
}
