.top-place-main-wrapper-section {
  padding: 70px 0;
}
.top-place-main-wrapper {
  width: 90%;
  margin: auto;
}
.top-place-main-wrapper-section .swiper-wrapper {
  text-align: center;
}
@media screen and (max-width: 450px) {
  .top-place-main-wrapper img {
    height: 150px;
    width: auto;
  }
}
@media screen and (min-width: 450px) and (max-width: 768px) {
  .top-place-main-wrapper img {
    height: 250px;
    width: auto;
  }
}
