.testimonials .parallax-bg {
  position: absolute;
  left: 0;
  top: 0;
  width: 130%;
  height: 100%;
  -webkit-background-size: cover;
  background-size: cover;
  background-position: center;
}
.testimonials .swiper-slide .subtitle {
  color: var(--white);
  text-align: center;
  font-size: 25px;
  padding: 50px 20px 15px 20px;
}
.testimonials .swiper-slide .text {
  color: var(--white);
  text-align: center;
  font-size: 15px;
  max-width: 700px;
  line-height: 1.5;
  padding: 15px 60px 50px 60px;
  margin: auto;
}
