.global-heading-section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  flex-wrap: nowrap;
  margin: auto;
  padding-bottom: 70px;
  width: 80%;
}
.global-section-main-heading {
  font-size: 30px;
  font-weight: normal;
  color: var(--dark);
}
.global-section-sub-heading {
  margin-top: 10px;
  font-size: 14px;
  color: var(--grey);
}
@media only screen and (max-width: 768px) {
    .global-heading-section {
        display: flex;
        flex-direction: column;
        width: 80%;
      }
}
.swiper-pagination-bullet{
  background: var(--white)!important;
  padding: 5px;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
input[type=number] {
  -moz-appearance: textfield;
}

input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:focus, 
input:-webkit-autofill:active{
    -webkit-box-shadow: 0 0 0 30px white inset !important;
}


/* registere & login css code */
.login-register-wrapper{
  background-color: var(--light-grey);
  padding: 70px 0;
}
.form-section{
  width: 35%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: auto;
  background-color: var(--white);
  border-radius: 20px;
  padding: 50px;
  box-shadow: 0 3px 6px rgb(57 95 157 / 15%), 0 2px 4px rgb(18 45 89 / 12%);
}
.login-register-wrapper .form-group{
  width: 100%;
  display: flex;
  flex-direction: column;
}
.login-register-wrapper .form-group h3{
  font-size: 18px;
  text-align: center;
  margin-bottom: 20px;
  color: var(--primary-color);
}
.login-register-wrapper .form-group input{
  outline: none;
  border: 1px solid var(--primary-color);
  padding: 15px 25px;
  width: calc(100% - 50px);
  margin: 7px 0 25px 0;
}
.login-register-wrapper .form-group label{
  margin: 3px 0;
}
.login-register-wrapper .form-group button{
  margin-top: 20px;
  background-color: var(--white);
  font-weight: bold;
  cursor: pointer;
}
.login-register-wrapper .form-group .forgot-password{
  background-color: var(--primary-color);
  color: var(--white);
  width: 80%;
  margin: auto;
  margin-top: 20px;
  border-radius: 30px;
}
@media screen and (max-width: 768px) {
  .login-register-wrapper .form-section{
      width: 60%;
  }
}