.discount-banner-main-wrapper-section {
  padding: 180px 0;
  background-color: var(--light-grey);
  position: relative;
  background-image: url(../images/offer-banner.png);
  background-repeat: no-repeat;
  background-size: 75% auto;
  height: 10vh;
  background-position: bottom left;
}
.discount-banner-main-wrapper-section img {
  /* position: absolute;
    bottom: 0;
    left: 0; */
}
.discount-banner-main-wrapper-section .discount-banner-content-wrapper {
  position: absolute;
  top: 5%;
  right: 5%;
  display: flex;
  flex-direction: column;
  padding: 30px;
  color: var(--primary-color);
  justify-content: center;
  align-items: center;
  z-index: 111;
}
.discount-banner-main-wrapper-section .discount-banner-content-wrapper h3 span {
  font-size: 50px;
}
.discount-banner-main-wrapper-section .discount-banner-content-wrapper h3 {
  font-size: 40px;
}
.discount-banner-main-wrapper-section .discount-banner-content-wrapper h5 {
  font-size: 30px;
}
.discount-banner-main-wrapper-section .discount-banner-content-wrapper button {
  outline: none;
  border: 3px solid var(--primary-color);
  border-radius: 20px;
  color: var(--dark);
  background-color: transparent;
  font-size: 15px;
  padding: 15px 20px;
  width: fit-content;
  margin-top: 20px;
  cursor: pointer;
}
@media only screen and (max-width: 700px) {
  .discount-banner-main-wrapper-section {
    padding: 100px 0;
    background-repeat: no-repeat;
    background-size: 100% auto;
    height: 10vh;
    background-position: bottom left;
  }
  .discount-banner-main-wrapper-section
    .discount-banner-content-wrapper
    h3
    span {
    font-size: 30px;
  }
  .discount-banner-main-wrapper-section .discount-banner-content-wrapper h3 {
    font-size: 20px;
  }
  .discount-banner-main-wrapper-section .discount-banner-content-wrapper h5 {
    font-size: 15px;
  }
  .discount-banner-main-wrapper-section
    .discount-banner-content-wrapper
    button {
    font-size: 14px;
    padding: 10px 20px;
    width: fit-content;
    margin-top: 10px;
    cursor: pointer;
  }
  .discount-banner-main-wrapper-section .discount-banner-content-wrapper {
    position: absolute;
    top: 0;
    right: 0;
    padding: 15px;
  }
}
@media only screen and (max-width: 1024px) and (min-width: 700px) {
  .discount-banner-main-wrapper-section {
    padding: 100px 0;
    background-repeat: no-repeat;
    background-size: 80% auto;
    height: 9vh;
    background-position: bottom left;
  }
  .discount-banner-main-wrapper-section
    .discount-banner-content-wrapper
    h3
    span {
    font-size: 40px;
  }
  .discount-banner-main-wrapper-section .discount-banner-content-wrapper h3 {
    font-size: 30px;
  }
  .discount-banner-main-wrapper-section .discount-banner-content-wrapper h5 {
    font-size: 20px;
  }
  .discount-banner-main-wrapper-section
    .discount-banner-content-wrapper
    button {
    font-size: 14px;
    padding: 10px 20px;
    width: fit-content;
    margin-top: 10px;
    cursor: pointer;
  }
  .discount-banner-main-wrapper-section .discount-banner-content-wrapper {
    position: absolute;
    top: 3%;
    right: 3%;
  }
}
