.slider-wrapper .swiper {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  margin: 0;
  padding: 0;
  border: 0;
  overflow-x: hidden;
  background: transparent;
  background-image: url(../images/header.png);
  background-repeat: no-repeat;
  background-size: cover;
  height: 90vh;
  --swiper-navigation-size: 30px;
  --swiper-theme-color: var(--white);
}
.swiper .swiper-wrapper {
  display: flex;
  flex-direction: row;
}
.swiper .slider-main-wrapper {
  position: relative;
  height: 100%;
}
.swiper .slider-main-wrapper img {
  position: absolute;
  bottom: 0;
  left: 0;
}
.swiper .slider-main-wrapper .content-wrapper-main {
  position: absolute;
  top: 0;
  left: 0;
  height: 90vh;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.swiper .slider-main-wrapper .content-wrapper {
  width: 80%;
}
.swiper .slider-main-wrapper .content-wrapper h1 {
  font-size: 35px;
  font-weight: lighter;
}
.swiper .slider-main-wrapper .content-wrapper h6 {
  margin: 10px 0;
  font-size: 17px;
  font-weight: lighter;
}
.swiper .slider-main-wrapper .content-wrapper button {
  outline: none;
  border: none;
  border: 2px solid var(--primary-color);
  padding: 7px 30px;
  border-radius: 10px;
  font-size: 16px;
}
@media only screen and (max-width: 700px) {
  .swiper .slider-main-wrapper img {
    display: none;
  }
}
@media only screen and (max-width: 1336px) and (min-width: 1024px) {
  .slider-wrapper .swiper {
    height: 91vh; 
  }
  .swiper .slider-main-wrapper .content-wrapper-main{
    height: 91vh; 
  }
}
