#personalInformationForm {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}

#personalInformationForm .card {
  box-shadow: 0 0 4px 0 hsl(0deg 0% 64% / 50%);
  padding: 1em 1em 0.5em;
  background: var(--white);
  margin: 10px 10px;
  width: calc(100% - 50px);
}
#personalInformationForm .card label {
  font-size: 14px;
  font-weight: 700;
}
#personalInformationForm .card .form-headings {
  margin: 0 10px;
}
#personalInformationForm .card .form-headings p {
  font-size: 14px;
  color: var(--grey);
}
#personalInformationForm .card .form-main-row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
#personalInformationForm .card .form-row {
  display: flex;
  flex-direction: column;
  transition: 0.5s all ease-in-out;
  flex-wrap: wrap;
  flex: 1 1 200px;
  margin: 10px 10px;
}
#personalInformationForm .card .form-row .form-row-content {
  display: flex;
  flex-direction: column;
}
#personalInformationForm .card .form-row input {
  padding: 10px 15px;
  margin: 10px 0;
  border: 1.35px solid var(--grey);
  outline: none;
  border-radius: 10px;
}
#personalInformationForm .button-group {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}
#personalInformationForm .button-group button {
  border: none;
  outline: none;
  border-radius: 15px;
  padding: 10px 20px;
  margin: 10px;
  color: var(--white);
  cursor: pointer;
}
#personalInformationForm .button-group .back {
  background-color: var(--primary-color);
}
#personalInformationForm .button-group .cancel {
  background-color: var(--danger);
}
#personalInformationForm .button-group .book {
  background-color: var(--success-color);
}
@media screen and (min-width: 1040px) {
  #personalInformationForm .card {
    width: 80%;
    margin: 10px auto;
  }
  #personalInformationForm .button-group {
    width: 84%;
    margin: auto;
  }
}
