/* css for vertical card with image */
.vertical-card{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  justify-content: left;
}
.vertical-card .card {
  display: flex;
  flex-direction: column;
  background-color: var(--white);
  cursor: pointer;
  border-radius: 5px;
  margin: 10px 10px;
  height: auto;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
  box-shadow: 0 3px 6px rgb(57 95 157 / 15%), 0 2px 4px rgb(18 45 89 / 12%);
  transition: 0.5s all ease-in-out;
  flex: 1 1 200px;
}
.vertical-card .card .card-header img{
  width: 100%;
  height: auto;
}
.vertical-card .card .card-body{
  padding: 20px;
}

/* css for vertical card with icon */

.vertical-card-with-icon {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  justify-content: left;
}
.vertical-card-with-icon .card {
  display: flex;
  flex-direction: column;
  /* flex-wrap: wrap; */
  background-color: var(--white);
  cursor: pointer;
  padding: 40px 30px;
  border-radius: 5px;
  margin: 10px 10px;
  height: auto;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
  box-shadow: 0 3px 6px rgb(57 95 157 / 15%), 0 2px 4px rgb(18 45 89 / 12%);
  transition: 0.5s all ease-in-out;
  flex: 1 1 200px;
}
.vertical-card-with-icon .card .card-header {
  font-size: 30px;
  background-color: var(--primary-color);
  color: var(--white);
  height: 70px;
  width: 70px;
  border-radius: 100%;
  text-align: center;
  line-height: 80px;
  margin-bottom: 30px;
  transition: 0.5s all ease-in-out;
}
.vertical-card-with-icon .card .card-body h4 {
  color: var(--dark);
  padding-bottom: 10px;
  font-size: 20px;
  text-transform: capitalize;
  transition: 0.5s all ease-in-out;
}
.vertical-card-with-icon .card .card-body a {
  color: var(--grey);
  transition: 0.5s all ease-in-out;
}
.vertical-card-with-icon .card .card-body p {
  color: var(--grey);
  font-size: 15px;
  transition: 0.5s all ease-in-out;
}
.vertical-card-with-icon .card:hover {
  background-color: var(--primary-color);
  color: var(--white);
  box-shadow: 0 3px 30px rgb(57 95 157 / 10%), 0 2px 26px rgb(18 45 89 / 8%);
}
.vertical-card-with-icon .card:hover .card-header {
  background-color: var(--white);
  color: var(--primary-color);
}
.vertical-card-with-icon .card:hover .card-body h4 {
  color: var(--white);
}
.vertical-card-with-icon .card:hover .card-body p {
  color: var(--white);
}
.vertical-card-with-icon .card:hover .card-body a {
  color: var(--white);
}

/* css for horizontal card */

.horizontal-card {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  justify-content: left;
}
.horizontal-card .card {
  display: flex;
  flex-direction: row;
  background-color: var(--white);
  cursor: pointer;
  padding: 40px 30px;
  border-radius: 5px;
  margin: 10px 10px;
  height: auto;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
  box-shadow: 0 3px 6px rgb(57 95 157 / 15%), 0 2px 4px rgb(18 45 89 / 12%);
  transition: 0.5s all ease-in-out;
  flex: 1 1 250px;
  justify-content: center;
  align-items: center;
}
.horizontal-card .card .card-header {
  background: transparent;
}
.horizontal-card .card .card-header img {
  transition: 0.5s all ease-in-out;
}
.horizontal-card .card:hover .card-header img {
  border-radius: 20px;
}
.horizontal-card .card .card-body {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: flex-start;
  vertical-align: middle;
  margin-left: 10px;
}
.horizontal-card .card .card-body h4 {
  color: var(--dark);
  padding-bottom: 10px;
  font-size: 20px;
  text-transform: capitalize;
  transition: 0.5s all ease-in-out;
}
.horizontal-card .card .card-body p {
  color: var(--grey);
  font-size: 15px;
  transition: 0.5s all ease-in-out;
}
.horizontal-card .card:hover {
  background-color: var(--primary-color);
  color: var(--white);
  box-shadow: 0 3px 30px rgb(57 95 157 / 10%), 0 2px 26px rgb(18 45 89 / 8%);
}
.horizontal-card .card:hover .card-body h4 {
  color: var(--white);
}
.horizontal-card .card:hover .card-body p {
  color: var(--white);
}

@media only screen and (max-width: 768px) {
  .horizontal-card .card .card-body h4 {
    font-size: 17px;
  }
  .horizontal-card .card .card-header img {
    width: auto;
    height: 160px;
  }
  .horizontal-card .card .card-body p {
    font-size: 13px;
  }
}
